import React from "react";

module.exports = React.createClass({

	getDefaultProps: function() {
		return {
			onClosePopover:function(){},
			context: null
		};
	},

	componentDidMount: function() {
		this.refs.container.getDOMNode().focus();
		this.positionInContext();
		setTimeout(this.scrollingFix, 1);
	},

	rectForDOMNode: function(node) {
		return {
			x: node.offsetLeft,
			y: node.offsetTop,
			width: node.offsetWidth,
			height: node.offsetHeight
		};

	},

	rect: function() {
		return this.rectForDOMNode(this.refs.popover.getDOMNode());
	},

	positionInContext: function() {

		if (this.props.context == null) {
			return this.positionCenteredInParent();
		}

		var context = this.props.context,
				rect = this.rect(),
				node = this.refs.popover.getDOMNode(),
				parent = node.offsetParent || document.body,
				containingRect = this.rectForDOMNode(parent),
				scrollingNode = this.refs.container.getDOMNode();	


		rect.x = (context.x + ((context.width - rect.width) * 0.5));
		rect.y = context.y + context.height + 24;

		// for now we display below
		node.style.left = rect.x + "px";
		node.style.top = rect.y + "px";

		rect.height = Math.min(containingRect.height - rect.y - 20, rect.height);
		node.style.height = rect.height + 'px';
	},

	positionCenteredInParent: function() {
		var node = this.refs.popover.getDOMNode(),
				parent = node.offsetParent || document.body,
				rect = this.rect();

		var containingRect = this.rectForDOMNode(parent);

		// first bound the sizes to max height and max width
		rect.width = Math.min(rect.width, containingRect.width - 40);
		rect.height = Math.min(rect.height, containingRect.height - 40);

		rect.x = (containingRect.width - rect.width) * 0.5;
		rect.y = (containingRect.height - rect.height) * 0.5;

		node.style.left = rect.x + "px";
		node.style.top = rect.y + "px";
		node.style.width = rect.width + "px";
		node.style.maxHeight = rect.height + "px";
	},

	onClickOutside: function(e) {
		e.preventDefault();
	},

	onBlur: function() {
		this.props.onClosePopover();
	},

	onFocus: function() {
	},

	scrollingFix: function() {
		this.refs.container.getDOMNode().style['-webkit-overflow-scrolling'] = 'touch';
	},

	onKeyDown: function(e) {
		if (e.which == 27) {
			this.props.onClosePopover();
		}
	},

	contextStyle: {
		position: 'absolute',
		top: '0',
		left: '0',
		right: '0',
		bottom: '0',
		zIndex: '1'
	},

	shimStyle: {
		position: 'absolute',
		top: '0',
		left: '0',
		right: '0',
		bottom: '0',
		zIndex: '-1',
		cursor: 'default',
		'-webkit-tap-highlight-color': 'transparent'
	},

	render: function() {
		return (
			<div className="popoverContext" style={this.contextStyle} onClick={this.onClickOutside}>
				<a href="#" onClick={this.onClickOutside} style={this.shimStyle}></a>
				<div ref="popover" className="popover">
					<div className="popover-scroll"
						ref="container"
						onKeyDown={this.onKeyDown}
						tabIndex="-1"
						onFocus={this.onFocus}
						onBlur={this.onBlur}>
						{this.props.children}
					</div>
				</div>
			</div>
		);
	}

});


